d.herokuapp.com/movimentacao/movimentacoes-usuario', { params: { login: this.usuario.login } })\n        .then((response) => {\n          this.historico_transferencias = response.data.movimentacoes\n          this.historico_transferencias.reverse(function (a, b) {\n            return parseFloat(a.id_transacao) - parseFloat(b.id_transacao)\n          })\n        })\n        .catch((error) => {\n          return alert(error)\n        })\n    },\n    formatDate (value) {\n      if (value) {\n        return moment(String(value)).format('DD/MM/YYYY HH:mm:ss')\n      }\n    },\n    formatPrice (value) {\n      let val = (value / 1).toFixed(2).replace('.', ',')\n      return val.toString().replace(/\\B(?=(\\d{3})+(?!\\d))/g, '.')\n    }\n  }\n}\n</script>\n\n <style scoped>\n.sign-up {\n  margin-top: 40px;\n}\ninput {\n  margin: 10px 0;\n  width: 20%;\n  padding: 15px;\n}\nbutton {\n  font-family: \"Roboto\", sans-serif;\n  text-transform: uppercase;\n  outline: 0;\n  background: #1606f3;\n  border: 0;\n  padding: 15px;\n  color: #ffffff;\n  font-size: 14px;\n  -webkit-transition: all 0.3 ease;\n  transition: all 0.3 ease;\n  cursor: pointer;\n}\nspan {\n  display: block;\n  margin-top: 20px;\n  font-size: 11px;\n}\n\n</style>\n"],"sourceRoot":""}]);

